// header
import commonHeader from"@/components/common-header/common-header";
export default {
	data() {
		return {
			selectCode: false,
			codeText: "获取验证码",
			disabled: false,
			treatyHide: false,
			codeHide: true,
			btnState: true,
			checked:true,
			phone:"",
			testCode:"",
			password:"",
			password1:"",
			phoneState:false,
			testCodeState:false,
			passwordState:false,
			password1State:false,
		}
	},
	methods: {
		// 获取输入手机号
		inputPhone(e) {
			if(!this.checked){
				uni.showToast({
					title:'请先勾选用户协议！'
				})
				return false;
			}
			if (e.detail.value.length === 11) {
				this.selectCode = true;
				this.phoneState = true;
				this.treatyHide = true;
			} else if (e.detail.value.length === 0) {
				this.treatyHide = false;
			} else {
				this.selectCode = false;
				this.phoneState = false;
			}
			this.phone = e.detail.value;
			this.loginBtnState();
		},
		// 获取输入验证码
		inputTestCode(e){
			if(e.detail.value.length === 6){
				this.testCodeState = true;
			}else{
				this.testCodeState = false;
			}
			this.testCode = e.detail.value;
			// console.log(e.detail.value)
			this.loginBtnState();
		},
		// 获取输入的密码
		inputPwd(e){
			if(e.detail.value.length === 6){
				this.passwordState = true;
			}else{
				this.passwordState = false;
			}
			this.password = e.detail.value;
			// console.log(e.detail.value)
			this.loginBtnState();
		},
		// 确认输入密码
		inputPwd1(e){
			this.password1 = e.detail.value;
			if(this.password1 === this.password){
				this.password1State = true;
			}else{
				this.password1State = false;
				uni.showToast({
					title:'两次密码不一致！',
					icon:'none'
				})
			}
			// console.log(e.detail.value)
			this.loginBtnState();
		},
		// 获取验证码
		getCode() {
			if (this.selectCode) {
				this.disabled = true;
				this.codeHide = false;
				var time = 59;
				this.codeText = time + 's后重新获取';
				var setTime = setInterval(() => {
					time--;
					this.codeText = time + 's后重新获取';
				}, 1000)
				setTimeout(() => {
					this.codeText = "获取验证码";
					this.disabled = false;
					this.codeHide = true;
					clearInterval(setTime);
				}, 59000)
			}
		},
		// 判断登录按钮状态
		loginBtnState(){
			if(this.phoneState&&this.testCodeState&&this.passwordState&&this.password1State){
				this.btnState = false;
			}else{
				this.btnState = true;
			}
		},
		// 注册
		goIndex() {
			uni.showToast({
				title:'绑定成功',
				success:()=>{
					uni.navigateTo({
						url:'../index/index'
					})
				}
			})
		}
	},
	components:{
		commonHeader
	}
}
